import React, { Component } from 'react'
import './index.css'
import pic1 from  '../../assets/img/gnms1@3x.png'
import pic2 from '../../assets/img/gnms2@3x.png'
import pic3 from '../../assets/img/gnms3@3x.png'
import qy from '../../assets/img/qy@3x.png'
import { Carousel,Collapse } from 'antd';

import Foot from '../foot'
export default class index extends Component {
  constructor(props){
    super(props)
    this.callback = this.callback.bind(this)
    this.state = {
      activeKey: '1'
    }
  }
  componentDidMount() {

  }

 callback(key) {
   console.log(key)
    this.setState({activeKey: key || this.state.activeKey})
  }
  render() {
    const Panel = Collapse.Panel;
    return (
      <div>
        <Carousel autoplay>
          <div><img src={pic1} alt="1"/><p>1</p></div>
          <div><img src={pic2} alt="2"/><p>2</p></div>
          <div><img src={pic3} alt="3"/><p>3</p></div>
         </Carousel>

         <div className="business">
           <div>
              <h1></h1>
              <h3></h3>
           </div>
           <div>
              <h1></h1>
              <h3></h3>
           </div>
           <div>
              <h1></h1>
              <h3></h3>
           </div>
         </div>

          <div className="advantage" >
          <h1></h1>
          <div className="advantageDetail">
            <div className="item">
              <img src={qy} alt=""/>
              <h3></h3>

            </div>
            <div className="item">
              <img src={qy} alt=""/>
              <h3></h3>

            </div>
            <div className="item">
              <img src={qy} alt=""/>
              <h3></h3>

            </div>
            <div className="item">
              <img src={qy} alt="Ready"/>
              <h3>Ready</h3>

            </div>
            <div className="item">
              <img src={qy} alt=""/>
              <h3></h3>

            </div>
            <div className="item">
              <img src={qy} alt="SLA"/>
              <h3>SLA</h3>

            </div>
          </div>
        </div>

        <div className="sence">
          <h1></h1>
          <Collapse accordion defaultActiveKey={['1']} activeKey={this.state.activeKey} onChange={this.callback}>
            <Panel header="" key="1">
              <img src={pic1} alt="1"/>
              <p>,</p>
            </Panel>
            <Panel header="" key="2">
              <img src={pic1} alt="2"/>
              <p>,</p>
            </Panel>
            <Panel header="" key="3" >
              <img src={pic1} alt="3"/>
              <p>,</p>
            </Panel>
            <Panel header="" key="4" >
              <img src={pic1} alt="3"/>
              <p>,</p>
            </Panel>
            <Panel header="" key="5" >
              <img src={pic1} alt="3"/>
              <p>,</p>
            </Panel>
          </Collapse>
        </div>

        <div className="function">
          <h1></h1>
          <h3>Fabric/Corda</h3>
          <h3>OrgChannel</h3>
          <h3></h3>
          <h3></h3>
          <h3></h3>
        </div>

        <Foot />
      </div>
    )
  }
}



